import { Table } from "reactstrap";
import { Link , useLocation} from "react-router-dom";
import "../views/Style.css";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Sideprofile from "../components/Sideprofile";
import { Icon } from "@iconify/react";
import {addEmployeInfoRequest,
  fetchAdminEmployeeInformation,
  fetchAdminProbation,
 fetchAdminWorkschedule,
  fetchAdminJobtimeline,
  fetchSideprofile,
} from "../action/action";

const EmloyeeJob = () => {
  const dispatch = useDispatch();
  const profile = useSelector((state) => state.profile.adminemployeeinformation
  );
  const probation = useSelector((state) => state.profile
  );
  const workschedule = useSelector((state) => state.profile);
  const jobtimeline = useSelector((state) => state.profile
  );
  const sideprofile = useSelector((state) => state.profile);
  const [editable, setEditable] = useState(false);
  const [probationInfo , setProbationInfo] =  useState(false)
  const [profileData, setProfileData] = useState({
  "IDS": "",
  "ServiceYear": "",
  "join_date": ""
});

  const location = useLocation();
  var employeeId = location.state;
  console.log("employeeid" ,  employeeId)
  useEffect(() => {
    // dispatch(fetchAttendance());
    dispatch(fetchAdminEmployeeInformation(employeeId));
    dispatch(fetchAdminProbation(employeeId));
    dispatch(fetchAdminWorkschedule(employeeId));
    dispatch(fetchAdminJobtimeline(employeeId));
    // dispatch(fetchSideprofile(employeeId));
  }, []);
  // if (!profile && !probation && !workschedule && !jobtimeline) {
  //   return <div>Loading...</div>;
  // }
  console.log("jobtimeline", jobtimeline);
  console.log(workschedule);
  console.log(probation);
  console.log(profile);
 
  const handleInputChange = (e, field) => {
    // Update the data of the field being edited
    console.log("handleInputChange called");
    setProfileData((prevProfileData) => ({
      ...prevProfileData,
      [field]: e.target.value,
    }));
  };
  const handleEditClick = () => {
    setEditable(true);
  };
  const handleProbationClick = () => {
    setProbationInfo(true);
  };
  const handleSaveClick = () => {
    // Perform the save functionality here, e.g., update the profile data on the server
    // After saving, set editable to false to deactivate all input fields
    setEditable(false);
    
    dispatch(addEmployeInfoRequest(employeeId, profileData));
   
  
  };
  const handleCancelClick = () => {
    // Discard any changes made while editing by reverting the data to the original profile data
    setProfileData(profile);
    // After canceling, set editable to false to deactivate all input fields
    setEditable(false);
    setProbationInfo(false)
  };
  if (!profile || !probation || !workschedule || !jobtimeline || !sideprofile) {
    // Render a loading state or return null while waiting for data
    return <div>Loading...</div>;
  }
  return (
    <>
      <div className="header">
        <div className="left-corner">
          <i className="user-icon "></i>
          <div>My Profile</div>
        </div>
      </div>
      <div className="wholeprofile" style={{ marginTop: "120px" }}>
        <Sideprofile />
        <div>
          <div class="TabMenu__TabWrapper-sc-8owevb-0 jfSAHT ">
            <ul height="50px" class="Tab__Tabs-yx1443-0 jjUJid">
              <li class="Tab__TabItem-yx1443-1 gJenVO">
                <Link to="/employeeprofile">
                  {" "}
                  <a>GENERAL</a>
                </Link>
              </li>
              <li class="Tab__TabItem-yx1443-1 hjzHTJ ">
                <a>Job</a>
              </li>
              <li class="Tab__TabItem-yx1443-1 gJenVO">
                <a>Documents</a>
              </li>
              <li class="Tab__TabItem-yx1443-1 gJenVO">
                <a>Dependents</a>
              </li>
              <li class="Tab__TabItem-yx1443-1 gJenVO">
                <a>Account Settings</a>
              </li>
            </ul>
          </div>

          <div class="section-container">
            <div class="header-container">
              <div class="flex-container">
                <Icon icon="uil:file-alt" color="#2ad25f" height={30} />
                <h5 class="section-title">Employe Information</h5>
                <div className="button-container">
                  <button
                    onClick={handleEditClick}
                    style={{ marginLeft: "5px" }}
                  >
                    Edit
                  </button>
                </div>
              </div>
            </div>
            <form method="POST" class="form-view">
              <div class="grid-container">
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Employee ID</span>
                  </div>
                  {editable ? (
                    <div className="field-content">
                      <input
                        className="field-input"
                        type="text"
                        value={profile.employeeID}
                        onChange={(e) => handleInputChange(e, "employeeID")}
                      />
                    </div>
                  ) : (
                    <div class="field-content">
                      <span class="field-value">{profile.employeeID}</span>
                    </div>
                  )}
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Join Date</span>
                  </div>
                   {editable ? (
                    <div className="field-content">
                      <input
                        className="field-input"
                        type="text"
                        value={profile.join_date}
                        onChange={(e) => handleInputChange(e, "join_date")}
                      />
                    </div>
                  ) : (
                    <div class="field-content">
                      <span class="field-value">{profile.join_date}</span>
                    </div>
                  )}
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Service Year</span>
                  </div>
                  {editable ? (
                    <div className="field-content">
                      <input
                        className="field-input"
                        type="text"
                        value={profile.serviceYear}
                        onChange={(e) => handleInputChange(e, "serviceYear")}
                      />
                    </div>
                  ) : (
                    <div class="field-content">
                      <span class="field-value">{profile.serviceYear}</span>
                    </div>
                  )}
                  <div class="field-footer"></div>
                </div>
              </div>
            </form>
            {editable ? (
        <div className="Profile-button-container">
          <button onClick={handleSaveClick}>Save</button>
          <button onClick={handleCancelClick}>Cancel</button>
        </div>
      ) : (
        <></>
      )}
          </div>
          <div class="section-container">
            <div class="header-container">
              <div class="flex-container">
                <Icon icon="uil:file-search-alt" color="#2ad258" height={30} />
                <h5 class="section-title">Probation Details</h5>
                <div className="button-container">
                  <button onClick={handleProbationClick} style={{ marginLeft: "50px" }}>Edit</button>
                </div>
              </div>
            </div>
            <form method="POST" class="form-view">
              <div class="grid-container">
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Probation Start Date</span>
                  </div>
                  {probationInfo ? (
                    <div className="field-content">
                      <input
                        className="field-input"
                        type="text"
                        value={probation.probationStartDate}
                        onChange={(e) => handleInputChange(e, "fullName")}
                      />
                    </div>
                  ) : (
                    <div class="field-content">
                      <span class="field-value">{probation.probationStartDate}</span>
                    </div>
                  )}
                  <div class="field-footer"></div>
                </div>

                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Probation End Date</span>
                  </div>
                  {probationInfo ? (
                    <div className="field-content">
                      <input
                        className="field-input"
                        type="text"
                        value={probation.probationEndDate}
                        onChange={(e) => handleInputChange(e, "fullName")}
                      />
                    </div>
                  ) : (
                    <div class="field-content">
                      <span class="field-value">{probation.probationEndDate}</span>
                    </div>
                  )}
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Line Manager</span>
                  </div>
                  {probationInfo ? (
                    <div className="field-content">
                      <input
                        className="field-input"
                        type="text"
                        value={probation.lineManagerName}
                        onChange={(e) => handleInputChange(e, "fullName")}
                      />
                    </div>
                  ) : (
                    <div class="field-content">
                      <span class="field-value">{probation.lineManagerName}</span>
                    </div>
                  )}
                  <div class="field-footer"></div>
                </div>
              </div>
            </form>
            {probationInfo ? (
        <div className="Profile-button-container">
          <button>Save</button>
          <button onClick={handleCancelClick}>Cancel</button>
        </div>
      ) : (
        <></>
      )}
          </div>
          <div class="section-container">
            <div class="header-container">
              <div class="flex-container">
                <Icon
                  icon="material-symbols:history"
                  color="#2ad258"
                  height={30}
                />
                <h5 class="section-title">Job Timeline</h5>
               
              </div>
            </div>
            <form method="POST" class="form-view">
              <Table
                className="no-wrap mt-3 align-middle "
                responsive
                borderless
              >
                <thead>
                  <tr>
                    <th>Effective Date</th>
                    <th>Job Title</th>
                    <th>Position Type</th>
                    <th>Employment Type</th>
                    <th>Line Manager</th>
                    <th>Department</th>
                    <th>Office</th>
                    <th>Note</th>
                  </tr>
                </thead>
                <tbody>
                  <tr className="border-top">
                    <td className="mb-0">{jobtimeline.effectiveDate}</td>

                    <td>{jobtimeline.jobTitle}</td>
                    <td>{jobtimeline.positionType}</td>
                    <td></td>
                    <td>{jobtimeline.lineManagerName}</td>

                    <td className="scrollable-td">
                      {jobtimeline.departmentName}
                    </td>
                    <td>{jobtimeline.officeName}</td>
                    <td>{jobtimeline.note}</td>
                  </tr>
                </tbody>
              </Table>
            </form>
          </div>
          <div class="section-container">
            <div class="header-container">
              <div class="flex-container">
                <Icon icon="uil:calendar-alt" color="#2ad258" height={30} />
                <h5 class="section-title">Work Schedule</h5>
              
              </div>
            </div>
            <form method="POST" class="form-view">
              <div class="grid-container">
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Bank Name</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">{}</span>
                  </div>
                  <div class="field-footer"></div>
                </div>

                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">
                      Standard working hours/day
                    </span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">
                      {workschedule.standardWorkingHoursPerDay}
                    </span>
                  </div>
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Schedule type</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">{workschedule.scheduleType}</span>
                  </div>
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">
                      Total working hours/week
                    </span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">
                      {workschedule.totalWorkingHoursPerWeek}
                    </span>
                  </div>
                  <div class="field-footer"></div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default EmloyeeJob;
